


.nav-list{
    display: flex ;
}

.nav-list__link a {
    font-family: Raleway;
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 15px;
    color: #FFFFFF;
}

.nav-list__link{
    margin-left: 45px;
}

.banner{
    height: 750px;
    background:url(../images/banner.png) center no-repeat;
    background-size: cover;
    position: relative;
    width: 100%;
    z-index: 0;
    &:before{
        content: "";
        z-index: 1;
        opacity: 0.8;
        height: 100%;
        width: 100%;
        position: absolute;
        background-color: #252B49;
    }
}


.header-logo{
    z-index: 2;
}

.header-block {
    display: flex;
    justify-content: space-between ;
    align-items: center;
    border-bottom: 1px solid #BABBC5;;
}

